.App {
  text-align: center;
}

.App-header {
  background-color: rgb(73, 73, 73);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-style: italic;
  color: white;
  text-shadow: 2px 10px 5px rgba(0, 0, 0, 0.3),
    0px -4px 5px rgba(255, 255, 255, 0.3);
}

.refl {
  -moz-transform: scaleY(-1);
  -o-transform: scaleY(-1);
  -webkit-transform: scaleY(-1);
  transform: scaleY(-1);
  -moz-transform: rotateX(210deg);
  -o-transform: rotateX(210deg);
  -webkit-transform: rotateX(210deg);
  transform: rotateX(210deg);
  perspective: 200px;

  -webkit-mask-image: -webkit-gradient(linear,
      right top,
      right bottom,
      from(transparent),
      color-stop(20%, transparent),
      to(rgba(0, 0, 0, 0.3)));
  mask-image: -webkit-gradient(linear,
      right top,
      right bottom,
      from(transparent),
      color-stop(20%, transparent),
      to(rgba(0, 0, 0, 0.3)));
}

.App {
  max-height: 80vh;
  overflow-y: auto;
}

.App-body {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.statistics {
  border: 2px solid rgb(122, 117, 117);
  padding: 5px;
  border-radius: 15px;
  max-width: 350px;
  margin: 20px auto;
  background-color: rgb(255, 255, 255);
}

.crypto-list {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1 {
  margin: 5px;
}

input,
button {
  border-radius: 3px;
  border-width: 1px;
}

input:focus {
  outline: none;
  border-color: #52494d;
  border-width: 2px;
  color: #52494d;
}

footer {
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  align-items: center;
  width: 100%;
  padding: 5px;
}

